<template>
	<div class="_page">
		<Header>
			<h1 slot="title">ÈÄÇËÄÅÂåñËÆ°ÁÆóÂô®</h1>
		</Header>
		<div class="computed flex-xb">
			<div class="ev source">
				<div class="head flex-xb">
					<p>ÈúÄË¶ÅËÆ°ÁÆóÁöÑÂÜÖÂÆπ</p>
					<el-button
						size="small"
						type="primary"
						icon="el-icon-setting"
						@click="switchConfig"
					>
						{{ hidenConfig ? '‰øùÂ≠òÂπ∂ÈáçËøõËÆ°ÁÆó' : 'ËÆæÁΩÆËÆ°ÁÆóËßÑÂàô' }}
					</el-button>
				</div>
				<textarea
					v-show="!hidenConfig"
					v-model="oldStyle"
				></textarea>
				<div
					class="config"
					v-show="hidenConfig"
				>
					<div class="content">
						<label>
							<span>Ëá™ÂÆö‰πâÂâçÁºÄ</span>
							<input v-model.trim="qianzhui" />
						</label>
						<label>
							<span>ÊñáÂ≠óÊúÄÂ∞èÂÄº</span>
							<input
								type="number"
								v-model.trim="sizeMin"
							/>
						</label>
						<label>
							<span>ÊñáÂ≠óËÆ°ÁÆóÊñπÊ≥ï</span>
							Value*<input
								type="number"
								v-model.trim="sizeComputed"
							/>
						</label>
						<label>
							<span>ÂÖ∂‰ªñÂ±ûÊÄßËÆ°ÁÆóÊñπÊ≥ï</span>
							Value*<input
								type="number"
								v-model.trim="outerComputed"
							/>
						</label>
					</div>
				</div>
			</div>
			<div class="ev result">
				<div class="head flex-xb">
					<p>ËÆ°ÁÆóÁªìÊûú:</p>
					<el-button
						size="small"
						type="primary"
						icon="el-icon-document-copy"
						@click="copyTextToClipboard($event)"
					>
						ÁÇπÂáªÂ§çÂà∂
					</el-button>
				</div>
				<pre><code id="Code" class="language-css" v-html="newStyle"></code></pre>
			</div>
		</div>
		<div :class="`tips ${tipShow ? 'show' : ''}`">‚úÖ Copy successfully !!! üåà</div>
	</div>
</template>

<script>
import '@/assets/css/arta.min.css';
import '@/assets/css/computed-elder.css';
import Header from '@/components/Header.vue';
import hljs from '@/assets/js/highlight.min.js';
export default {
	components: { Header },
	data() {
		return {
			oldStyle: `
.home1 {
    border-bottom: 1px solid #ECEEF2;padding-bottom: 7px;
}
.home1 .top,
.home1 .top .asdf{
    margin: 12px 0 9px;
    font-size: 14px;
}`,
			sizeMin: 18,
			qianzhui: '.elder',
			sizeComputed: 1.5,
			outerComputed: 1.5,
			hidenConfig: false,
			tipShow: false,
			newStyle: '',
		};
	},
	watch: {
		oldStyle: {
			handler: function (_new, _old) {
				this.createdNewStyle();
			},
			immediate: true,
		},
	},
	methods: {
		switchConfig() {
			this.hidenConfig = !this.hidenConfig;
			this.createdNewStyle();
		},
		createdNewStyle() {
			let styleList = this.oldStyle;
			// styleList = styleList.replaceAll("}", "}@@@");
			styleList = styleList.replace(/}/g, '}@@@');
			styleList = styleList.split('@@@');
			let allStyle = '';
			styleList.forEach((styleEv) => {
				// Âä†‰∏äÂâçÁºÄ
				styleEv = styleEv.replace(/.*(-,|{)/g, ($1) => {
					// ÂøΩÁï•Â™í‰ΩìÊü•ËØ¢
					if ($1.indexOf('@media') != -1) {
						return $1;
					}
					// ÂøΩÁï•Âä®Áîª
					if ($1.indexOf('@keyframes') != -1) {
						return $1;
					}
					// ÂøΩÁï•Âä®ÁîªÂÜÖÈÉ®ÁöÑfromÔºåto,0%,25%...
					if (/^[from|to|\d]/g.test($1.trim())) {
						return $1;
					}
					console.log($1);
					let res = $1.trim();
					let trimLength = $1.length - res.length;
					let trimStr = '';
					for (var i = 0; i < trimLength; i++) {
						trimStr += ' ';
					}
					return trimStr + this.qianzhui + ' ' + res;
				});
				styleEv = styleEv.replace(/(?<={)(.*\s*(?!}))*/g, ($1) => {
					// ÊãÜÂàÜÔºåËé∑ÂèñÂà∞ÊØè‰∏™Â±ûÊÄß
					// let arr = $1.replaceAll(";", ";@@@").split("@@@");
					let arr = $1.replace(/;/g, ';@@@').split('@@@');
					let str = '';
					arr.forEach((line, i) => {
						if (line.indexOf('font-size') != -1) {
							str += computedPX(line, this.sizeComputed, this.sizeMin);
						} else {
							str += computedPX(line, this.outerComputed);
						}
					});
					return str;
				});
				allStyle = allStyle + styleEv;
			});
			/*
			 * line Ë¶ÅÊü•ÊâæÁöÑÁõÆÊ†áÔºå‰æãÂ¶ÇÔºömargin: 12px 0 9px;
			 * formula ËÆ°ÁÆóÁöÑÊï∞ÂÄºÔºå‰æãÂ¶Ç 0.8
			 * minSize ÊúÄÂ∞èÂÄºÔºåÂ¶ÇÊûúÂ∞è‰∫éÊúÄÂ∞èÂÄºÂàô‰ΩøÁî®ÊúÄÂ∞èÂÄº
			 */
			function computedPX(line, formula, minSize) {
				if (minSize) {
					return line.replace(/(\d+px)/g, ($1) => {
						if (parseFloat($1) < minSize) {
							return minSize + 'px';
						}
						return Math.ceil(parseFloat($1) * formula) + 'px';
					});
				}
				return line.replace(/(\d+px)/g, ($1) => {
					return Math.ceil(parseFloat($1) * formula) + 'px';
				});
			}

			this.newStyle = allStyle;
			// if (process.client) {
			this.$nextTick(() => {
				let el = document.getElementById('Code');
				hljs.highlightElement(el);
			});
			// }
		},
		copyTextToClipboard(event) {
			let that = this;
			this.tipShow = false;
			let text = this.newStyle;
			if (!navigator.clipboard) {
				function fallbackCopyTextToClipboard(text) {
					// 1.ÂàõÂª∫‰∏Ä‰∏™ÂèØÈÄâ‰∏≠ÂÖÉÁ¥†
					let textArea = document.createElement('textarea');
					textArea.value = text;
					// 2.‰ΩøÁî®ÂÆö‰ΩçÔºåÈòªÊ≠¢È°µÈù¢ÊªöÂä®
					textArea.style.top = '0';
					textArea.style.left = '0';
					textArea.style.position = 'fixed';
					document.body.appendChild(textArea);
					textArea.focus();
					textArea.select();
					try {
						var successful = document.execCommand('copy');
						var msg = successful ? 'successful' : 'unsuccessful';
						// console.log('Fallback: Copying text command was ' + msg);
						that.tipShow = true;
						var iii = setTimeout(() => {
							that.tipShow = false;
							clearTimeout(iii);
						}, 1000);
					} catch (err) {
						console.error('Fallback: Oops, unable to copy', err);
					}
					// 3.ÁßªÈô§ÂÖÉÁ¥†
					document.body.removeChild(textArea);
				}
				fallbackCopyTextToClipboard(text);
				return;
			}
			navigator.clipboard.writeText(text).then(
				function () {
					// console.log('Async: Copying to clipboard was successful!');
					that.tipShow = true;
				},
				function (err) {
					console.error('Async: Could not copy text: ', err);
				}
			);
		},
	},
};
</script>
